import React, { useState, useEffect } from 'react';

import { useSelector, useDispatch } from 'react-redux'
import ImageGrid from './ImageGrid';
import downloadjs from 'downloadjs';
import html2canvas from 'html2canvas'

import { VisionSliderData } from '../../Data/VisionData';
import { BackgroundSliderData } from '../../Data/BackgroundData';
import { DnaSliderData } from '../../Data/DnaData';



export default function Canvas () {

  //ALL STATES
  const globalVision = useSelector((state) => state.vision.currentVision);
  const globalBackground = useSelector((state) => state.background.currentBackground);
  const globalDna = useSelector((state) => state.dna.currentDna);


  const [currentGroup, setCurrentGroup] = useState(VisionSliderData);
  function changeDna () {
    setCurrentGroup(DnaSliderData)
  }
  
  function changeBg () {
    setCurrentGroup(BackgroundSliderData)
  }

  function changeVision () {
    setCurrentGroup(VisionSliderData)
  }



  function Download() {
    let div = document.getElementById('photo');
    html2canvas(div).then(
        function (canvas) {
            document.getElementById('output').appendChild(canvas);
            const dataURL = canvas.toDataURL('image/png');
            downloadjs(dataURL, 'download.png', 'image/png');
            document.getElementById('output').removeChild(canvas)
        })  
  }


  
  return (
    <>
      <div className = "Canvas" >
        <div id="photo" >

          <img src={globalBackground} alt="currentImage" className='Background downsize' />
          <img src={globalDna} alt="currentImage" className='Dna downsize' />
          <img src={globalVision} alt="currentImage" className='Vision downsize' />


        </div>

    

        <h1>Output:</h1>
        <div id="output"></div>
      </div>

      
      {/* <section>
            {renderImages()}
      </section> */}


      <section className = "temp">
        <ImageGrid 
          dataArray={currentGroup.data}
          name={currentGroup.name}
        />
      </section>

      <button onClick={()=>Download()}>
          Download
        </button>

      <button onClick={()=>changeDna()}>DNA SWITCH </button>
      <button onClick={()=>changeBg()}>BACKGROUD SWITCH</button>
      <button onClick={()=>changeVision()}>VISION SWITCH</button>
    
    </>
  );
}
